import { Injectable } from "@angular/core";
import { map, filter } from "rxjs/operators";
import {io} from 'socket.io-client/build/index';
import { Subject, BehaviorSubject } from "rxjs";
import { DoctorService } from "../doctor-service/doctor.service";
import { environment } from "../../../environments/environment";

@Injectable({
  providedIn: "root",
})
export class SocketServiceService {
  private socket;
  private doctorCalling: Subject<any> = new BehaviorSubject<any>(null);
  constructor(doctorService: DoctorService) {
    console.log("Socket service");
    doctorService.doctorUsernameChanged$.subscribe((data) => {
      if (data) {
        this.disconnect();
      }
    });
  }
  get doctorCalling$() {
    return this.doctorCalling.asObservable().pipe(filter((node) => !!node));
  }
  get isSocketConnected() {
    return this.socket ? this.socket.connected : false;
  }
  connect(patientId: string, doctorMobile: string, doctorId: string) {
    this.disconnect();

    this.socket = io(environment.apiEndpoint, {
      query: {
        userId: patientId,
        doctorId: doctorId,
      },
      transports: ["websocket"],
    });
    this.socket.on("connect", (msg) => {
      console.log("connect called");
      this.joinRoom(doctorMobile);
    });
    this.socket.on("doctorCalling", () => {
      console.log("doctor calling");

      this.doctorCalling.next(true);
    });
    this.socket.on("disconnect", () => {
      console.log("disconnected");
    });
  }
  disconnect() {
    console.log("disconnecting");
    try {
      if (this.socket) {
        this.socket.io.removeAllListeners();
        this.socket.disconnect();
        this.socket.close();
        this.socket = null;
      }
    } catch (error) {
      console.log(error);
    }
  }
  joinRoom(roomName: string) {
    this.socket.emit("joinRoom", {
      roomName: roomName,
    });
    console.log("joining room", roomName);
  }
  ngOnDestroy(): void {
    this.disconnect();
  }
}
