import { Component, Input, OnInit } from '@angular/core';
import { DoctorCMS } from 'src/app/interface/doctor-cms';
import { Doctor } from '../../../interface/doctor';
import { MediaControlService } from '../../../shared/UI_service/media-control.service';
@Component({
  selector: 'app-overview',
  templateUrl: './overview.component.html',
  styleUrls: ['./overview.component.less']
})
export class OverviewComponent implements OnInit {
  @Input() doctor: Doctor;
  @Input() drCMS: DoctorCMS;
  @Input() location;

  Overview: any[] = [
    { categoryName: 'About' },
    { categoryName: 'Work Experience' },
    { categoryName: 'Speciality' },
  ]

  constructor(private _viewMedia: MediaControlService) { }
  lists = ['About', 'Work Experience', 'Speciality']
  tab_toggle: boolean = true;
  public thisDevice: boolean;
  hGutter = 70;
  vGutter = 70;
  ngOnInit(): void {
    console.log(this.drCMS);

  }
  dataToggle(param) {
    this.tab_toggle = param;
  }
  public collapseNum: number = 0;
  collapse(param) {
    this.collapseNum = param;
  }
  ngAfterContentChecked(): void {
    this.thisDevice = this._viewMedia.mediaPort();
    if (this._viewMedia.winMedia) {
      this.hGutter = 10;
      this.vGutter = 10;
    }
  }
}
