import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { LoginComponent } from './auth/login/login.component';
import { RegisterComponent } from './auth/register/register.component';
import { DoctorPortalComponent } from './doctor-portal.component';
import { AppComponent } from '../app.component';
import { DoctorRootComponent } from './doctor-root/doctor-root.component'
import { LoginAuthGuard } from "../guards/login-auth-guard.service";
import { AuthGuard } from "../guards/auth-guard.service";
import { PrivacyPolicyComponent } from './other_page/privacy-policy/privacy-policy.component';
import { TermsOfUseComponent } from './other_page/terms-of-use/terms-of-use.component';
const routes: Routes = [

  {
    path: ":username",
    component: DoctorRootComponent,
    children: [
      {
        path: "",
        component: DoctorPortalComponent,
        children: [
          { path: "Login", component: LoginComponent, canActivate: [LoginAuthGuard], },
          {
            path: "Login/:token",
            component: LoginComponent,
            canActivate: [LoginAuthGuard],
          },

        ]
      },
      { path: "Register/:mobile", component: RegisterComponent, },
      {
        path: 'dashboard', data: { preload: false }, loadChildren: () => import('../patient-dashboard/patient-dashboard.module').then(m => m.PatientDashboardModule), canActivate: [AuthGuard],
      },
      { path: 'privacypolicy', component: PrivacyPolicyComponent },
      { path: 'termsofuse', component: TermsOfUseComponent }
    ]
  }
  //   { path: ':username', component: DoctorPortalComponent,
  //   children: [
  //     { path: 'Login', component: LoginComponent },
  //   { path: 'Register', component: RegisterComponent }
  //   ],
  // }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DoctorPortalRoutingModule { }
