import { Component, Input, OnInit } from '@angular/core';
import { MediaControlService } from '../../../shared/UI_service/media-control.service'
@Component({
  selector: 'app-treatements',
  templateUrl: './treatements.component.html',
  styleUrls: ['./treatements.component.less']
})
export class TreatementsComponent implements OnInit {

  @Input() treatments:treateMent[];

  tab_toggle: boolean = true;
  thisDevice: boolean;
  hGutter = 70;
  vGutter = 70;

  panel: any[] = [1, 2, 3, 4, 5, 6]
  constructor(private _viewMedia: MediaControlService) { }

  ngOnInit(): void {
  }
  ngAfterContentChecked(): void {
    this.thisDevice = this._viewMedia.mediaPort();
    if (this._viewMedia.winMedia) {
      this.hGutter = 10;
      this.vGutter = 10;
    }
  }
  dataToggle(param) {
    this.tab_toggle = param;
  }

  public collapseNum: number = 0;
  collapse(param) {
    this.collapseNum = param;
  }
}

export interface treateMent {
  categoryName:string;
  subCategories:string[];
}