import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Subscription } from 'rxjs';
import { Clinic } from '../interface/clinic';
import { Doctor } from '../interface/doctor';
import { LandingPageData } from '../interface/landingData';
import { TelemedicineSetup } from '../interface/telemedicine-setup';
import { DoctorService } from '../services/doctor-service/doctor.service';
import { MediaControlService } from '../shared/UI_service/media-control.service';
import * as moment from "moment";
import { DoctorCMS } from '../interface/doctor-cms';
import { ChangeDetectionStrategy } from '@angular/compiler/src/compiler_facade_interface';
import { DynamicTitleService } from '../shared/UI_service/dynamic-title.service';
import { DivScrollService } from '../shared/UI_service/div-scroll.service'
import { GetDrNameService } from '../shared/UI_service/get-dr-name.service';
@Component({
  selector: 'app-doctor-portal',
  templateUrl: './doctor-portal.component.html',
  styleUrls: ['./doctor-portal.component.less']
})
export class DoctorPortalComponent implements OnInit {

  side_page_nav: boolean;
  haveChatBox: boolean = true;

  doctor: Doctor;
  clinics: Clinic[];
  actualClinic: Clinic;
  todayDateStr: string = "";
  isMobile: boolean = window.screen.width < 601;
  isAuthenticated = false;
  telemedicineSetup: TelemedicineSetup;
  doctorCms: DoctorCMS;
  constructor(private doctorService: DoctorService, private _viewMedia: MediaControlService, public router: Router, private cdf: ChangeDetectorRef, private _dynamicTitle: DynamicTitleService, private _divScroll: DivScrollService, private _dr: GetDrNameService) { }
  landingPageDataObservable: Subscription;
  days = [
    "Monday",
    "Tuesday",
    "Wednesday",
    "Thursday",
    "Friday",
    "Saturday",
    "Sunday",
  ];
  allStartTime: any[] = [];

  ngOnInit(): void {

    const token = localStorage.getItem("token-" + this.doctorService.username);
    if (token) {
      this.isAuthenticated = true;
    }
    this.landingPageDataObservable = this.doctorService.data$.subscribe((d) => {
      this.handleLandingPageData(d);
    });


  }

  ngAfterContentChecked(): void {
    this.side_page_nav = this._viewMedia.mediaPort()
    this.ifLogin()
  }
  public forlogin: boolean;
  ifLogin() {
    let urlStr = `/home/${this._dr.getDr()}`;
    if (
      this.router.url == urlStr) {
      this.forlogin = true;
    } else {
      this.forlogin = false;
    }
  }
  chatBox(param) {
    this.haveChatBox = param;
  }

  // ________ page scroll Spy ____________
  public DivName = this._divScroll.sectionName;
  socroll(divId: string) {
    this._divScroll.triggerScrollTo(divId)
  }


  handleLandingPageData(d: LandingPageData) {
    this.doctor = d.doctor;
    this.doctorCms = d.doctorCMS;
    console.log(d.doctorCMS);

    this.telemedicineSetup = d.telemedicineSetup;
    localStorage.setItem(
      `doctor-${this.doctorService.username}`,
      JSON.stringify(this.doctor)
    );
    const todayDate = moment();
    this.todayDateStr = todayDate.format("LL");
    const todayDay = todayDate.format("dddd").toLowerCase();
    this.clinics = d.clinics;
    this.actualClinic = d.actualClinic;
    this.clinics.sort((a, b) => {
      return b.timestamp - a.timestamp;
    });

    for (let index1 = 0; index1 < this.clinics.length; index1++) {
      const clinic = this.clinics[index1];
      clinic.businessHours = [];
      this.days.forEach((x) => {
        clinic.businessHours.push({ day: x, timings: [], timingsStr: [] });
      });

      for (let index2 = 0; index2 < clinic.timings.length; index2++) {
        const clinicTiming = clinic.timings[index2];
        for (let index3 = 0; index3 < clinic.businessHours.length; index3++) {
          const bh = clinic.businessHours[index3];
          let isWorking = false;
          if (clinicTiming.monStatus && bh.day == "Monday") {
            isWorking = true;
          }
          if (clinicTiming.tueStatus && bh.day == "Tuesday") {
            isWorking = true;
          }
          if (clinicTiming.wedStatus && bh.day == "Wednesday") {
            isWorking = true;
          }
          if (clinicTiming.thuStatus && bh.day == "Thursday") {
            isWorking = true;
          }
          if (clinicTiming.friStatus && bh.day == "Friday") {
            isWorking = true;
          }
          if (clinicTiming.satStatus && bh.day == "Saturday") {
            isWorking = true;
          }
          if (clinicTiming.sunStatus && bh.day == "Sunday") {
            isWorking = true;
          }
          if (isWorking) {
            bh.timings.push({
              startTime: clinicTiming.starttime,
              endTime: clinicTiming.endtime,
            });
            bh.timingsStr.push(
              moment(clinicTiming.starttime, "HH:mm").format("hh:mm A") +
              "-" +
              moment(clinicTiming.endtime, "HH:mm").format("hh:mm A")
            );
          }
        }
      }
      const businessHour = clinic.businessHours.find(
        (x) => x.day.toLowerCase() == todayDay
      );

      if (businessHour.timings.length) {
        // const moments = [];
        const currentTime = moment();
        for (let index = 0; index < businessHour.timings.length; index++) {
          const timing = businessHour.timings[index];
          const startTimeMoment = moment(timing.startTime, "HH:mm");
          const endTimeMoment = moment(timing.endTime, "HH:mm");

          this.allStartTime.push({ time: startTimeMoment.format("hh A"), times: startTimeMoment.format("HH") });

          if (currentTime.isBetween(startTimeMoment, endTimeMoment)) {
            clinic.todayWorkingTime =
              "Closes at " + endTimeMoment.format("hh:mm A");
            break;
          } else if (currentTime.isBefore(startTimeMoment)) {
            clinic.todayWorkingTime =
              "Opens at " + startTimeMoment.format("hh:mm A");
            break;
          }
        }

        clinic.isTodayWorking = false;

        businessHour.timings.forEach((time) => {
          const startTime = moment(
            currentTime.format("DD/MM/YYYY") + " " + time.startTime,
            "DD/MM/YYYY HH:mm"
          );
          const endTime = moment(
            currentTime.format("DD/MM/YYYY") + " " + time.endTime,
            "DD/MM/YYYY HH:mm"
          );
          if (currentTime.isBetween(startTime, endTime)) {
            clinic.isTodayWorking = true;
          }
        });
      }
    }


    this.allStartTime.sort((a, b) => {
      return a.times - b.times;
    });


    const clinicCity = this.clinics.find((item) => {

      return item.city != ''
    })

    if (clinicCity) {
      this.location = `${clinicCity.city}, ${clinicCity.state}`
    }

    this.cdf.detectChanges();

  }

  location: string = '';
}
