import { ChangeDetectorRef, Component, Inject, NgZone, OnInit } from '@angular/core';

import { ActivatedRoute, Router } from "@angular/router";
import { Subscription } from "rxjs";
import { BaseHttpService } from "../../services/base-http.service";
import { HttpConstants } from "../../services/http-constants";
import { Doctor } from "../../interface/doctor";
import { LandingPageData } from "../../interface/landingData";
import { ClinicTime } from "../../interface/clinic-timing";
import { DoctorService } from "../../services/doctor-service/doctor.service";
import { SocketServiceService } from "../../services/patient-service/socket-service.service";
import { PatientService } from "../../services/patient-service/patient.service";
import { Appointment } from "../../interface/appointment";
import { LazyService } from '../../services/lazy.service';
import { NzMessageService } from 'ng-zorro-antd/message';
import { DOCUMENT } from '@angular/common';
import { MediaControlService } from '../../shared/UI_service/media-control.service';
import { NzDrawerPlacement } from 'ng-zorro-antd/drawer';
import { NzModalService } from 'ng-zorro-antd/modal';
import { DivScrollService } from 'src/app/shared/UI_service/div-scroll.service';
import { GetDrNameService } from 'src/app/shared/UI_service/get-dr-name.service';

@Component({
  selector: 'app-doctor-root',
  templateUrl: './doctor-root.component.html',
  styleUrls: ['./doctor-root.component.less']
})
export class DoctorRootComponent implements OnInit {

  username: string;
  private sub: Subscription;

  appointment?: Appointment;
  doctor?: Doctor;
  patientWaitingForCall: Subscription;
  userJoinedRoom: Subscription;
  userAlreadyInRoom: Subscription;



  title = 'Welcome Dr. Home';
  isCollapsed = true;
  hamburger: boolean;
  side_page_nav: boolean;
  isport: boolean;
  haveChatBox: boolean = true;
  loading = false;


  constructor(
    private route: ActivatedRoute,
    public route_: Router,
    private modal: NzModalService,
    private httpService: BaseHttpService,
    private doctorService: DoctorService,
    private socketService: SocketServiceService,
    private patientService: PatientService,
    private lazy: LazyService,
    private msg: NzMessageService,
    private zone: NgZone,
    private cdr: ChangeDetectorRef,
    @Inject(DOCUMENT) private doc: any,
    private _viewMedia: MediaControlService,
    private _divScroll: DivScrollService,
    private _dr: GetDrNameService
  ) {

    this.sub = this.route.params.subscribe((params) => {
      this.username = params["username"]; // (+) converts string 'id' to a number
      this.doctorService.username = this.username;
      this._dr.Drname(this.username)
      console.log(this.username);
      localStorage.setItem('DoctorName', this.username)
      this.getDoctorLandingPageData(this.username);

    });


  }

  async ngOnInit() {


  }

  getDoctorLandingPageData(username: string) {
    this.httpService
      .makeAuthRequest<LandingPageData>(
        "GET",
        HttpConstants.doctor.getDoctorLandingPageData + username
      )
      .subscribe((response) => {
        if (response.hasErrors()) {
          this.route_.navigate(['/error']);
        } else {

          if (response.data.doctorCMS) {

            if (response.data.doctorCMS.primaryColor) {
              this.changeColor(response.data.doctorCMS.primaryColor);
            }

          }

          const clinicTimes: ClinicTime[] = [];
          for (
            let index = 0;
            index < response.data.clinicTimings.length;
            index++
          ) {
            const clinicTiming = response.data.clinicTimings[index];
            const days: string[] = [];
            let daysNumber: number[] = [];

            if (
              clinicTiming.monStatus &&
              clinicTiming.tueStatus &&
              clinicTiming.wedStatus &&
              clinicTiming.thuStatus &&
              clinicTiming.friStatus &&
              clinicTiming.satStatus &&
              clinicTiming.sunStatus
            ) {
              days.push("Mon - Sun");
              daysNumber = [0, 1, 2, 3, 4, 5, 6]
            } else {
              if (clinicTiming.monStatus) {
                days.push("Mon");
                daysNumber.push(1)
              }
              if (clinicTiming.tueStatus) {
                days.push("Tue");
                daysNumber.push(2)

              }
              if (clinicTiming.wedStatus) {
                days.push("Wed");
                daysNumber.push(3)

              }
              if (clinicTiming.thuStatus) {
                days.push("Thu");
                daysNumber.push(4)

              }
              if (clinicTiming.friStatus) {
                days.push("Fri");
                daysNumber.push(5)

              }
              if (clinicTiming.satStatus) {
                days.push("Sat");
                daysNumber.push(6)

              }
              if (clinicTiming.sunStatus) {
                days.push("Sun");
                daysNumber.push(0)

              }
            }
            const clinicTime: ClinicTime = {
              clinicId: clinicTiming.clinicId,
              clinicTimingId: clinicTiming.doc_id,
              timings: `${clinicTiming.starttime} - ${clinicTiming.endtime}`,
              days: days.join(", "),
              disableDays: daysNumber,
              isVirtualClinic: clinicTiming.isVirtualClinic,
              ...clinicTiming
            };
            clinicTimes.push(clinicTime);
          }

          for (let index = 0; index < response.data.clinics.length; index++) {
            const clinic = response.data.clinics[index];
            const filteredClinicTimings = clinicTimes.filter(
              (x) => x.clinicId == clinic.doc_id
            );
            const filteredTimings = response.data.clinicTimings.filter(
              (x) => x.clinicId == clinic.doc_id
            );
            clinic.clinicTimings = filteredClinicTimings;
            clinic.timings = filteredTimings;
          }
          {
            const virtualClinic = response.data.clinics.find(
              (x) => x.isVirtualClinic
            );
            response.data.virtualClinic = virtualClinic;
          }

          {
            let actualClinic = response.data.clinics.find(
              (x) => !x.isVirtualClinic
            );
            if (!actualClinic) {
              actualClinic = response.data.virtualClinic;
            }
            response.data.actualClinic = actualClinic;
          }

          this.doctorService.pushData(response.data);
          const appointment = response.data.appointment;
          const doctor = response.data.doctor;
          if (appointment) {
            // this.socketService.connect(
            //   appointment.patient_id,
            //   doctor.doc_mobile,
            //   doctor.uid
            // );
          }
          //this.subscribeToSocketRelationSubscriptions();
        }
      });
  }

  ngOnDestroy() {
    this.sub.unsubscribe();
  }





  changeColor(color: string): void {
    return this.runLess(color);
  }

  private runLess(color): void {
    const { zone, msg, cdr } = this;
    const msgId = msg.loading(`Setup Theme`, { nzDuration: 0 }).messageId;
    setTimeout(() => {
      zone.runOutsideAngular(() => {
        return this.loadLess()
          .then(() => {
            (window as any).less
              .modifyVars({
                '@primary-color': color,
              })
              .then(() => {
                msg.success('Success');
                msg.remove(msgId);
                zone.run(() => cdr.detectChanges());
              });
          })
          .catch((err) => {
            msg.error(err.message);
            msg.remove(msgId);
          });
      });
    }, 200);
  }

  private loadLess(): Promise<void> {
    if ((window as any).less) {
      return Promise.resolve();
    }
    return this.lazy
      .loadStyle('./assets/default.less', 'stylesheet/less')
      .then(() => {
        const lessScript = this.doc.createElement('script');
        lessScript.innerHTML = `
          window.less = {
            async: true,
            env: 'production', // development
            javascriptEnabled: true
          };
        `;
        this.doc.body.appendChild(lessScript);
      })
      .then(() =>
        this.lazy
          .loadScript(
            './assets/less.min.js'
            //'https://gw.alipayobjects.com/os/lib/less.js/3.8.1/less.min.js'
          )
          .catch(() => this.lazy.loadScript('./assets/less.min.js'))
      )
      .then(() => { });
  }

  public navBar_option;
  public urlMatch;
  public drName = this._dr.getDr();
  ngAfterContentChecked(): void {
    this.hamburger = this._viewMedia.mediaPort()
    this.side_page_nav = this._viewMedia.mediaPort()
    this.isport = this._viewMedia.mediaPort()
    if (localStorage.getItem(`token-${this._dr.getDr()}`)) {
      this.LogoutBtn = true;
    }
    // 
    let urlStr = `/home/${this._dr.getDr()}`;

    this.urlMatch = urlStr;
    if (
      this.route_.url == `${urlStr}/dashboard` ||
      this.route_.url == `${urlStr}`) {
      this.navBar_option = true;
    } else {
      this.navBar_option = false;
    }
  }
  chatBox(param) {
    this.haveChatBox = param;
  }
  backToDrPage() {
    this.route_.navigateByUrl(`/home/${this._dr.getDr()}`)
  }
  goToLink(url) {
    this.route_.navigate(['./dashboard'], {
      replaceUrl: true,
      relativeTo: this.route,
    });

    this.isCollapsed = false

  }
  // TODO side bar 
  public visible = false;
  placement: NzDrawerPlacement = 'left';
  open(): void {
    this.visible = true;
  }

  close(): void {
    this.visible = false;
  }

  // Logout 

  logoutConfirm(): void {
    this.modal.confirm({
      nzTitle: 'Are you sure you want to Logout?',
      // nzContent: '<b style="color: red;">Some descriptions</b>',
      nzOkText: 'Yes',
      nzOkType: 'primary',
      nzOnOk: () => this.logout(),
      nzCancelText: 'No',
      nzOnCancel: () => console.log('Cancel')
    });
  }
  LogoutBtn: boolean;
  private logout() {
    localStorage.removeItem(`token-${this._dr.getDr()}`)
    this.route_.navigate(["."])
    this.LogoutBtn = false;
  }

  // ________ page scroll Spy ____________
  public DivName = this._divScroll.sectionName;
  socroll(divId: string) {
    this._divScroll.triggerScrollTo(divId)
  }
}
