import { Component, Input, OnInit } from '@angular/core';
import { Clinic } from '../../../interface/clinic';
import { MediaControlService } from '../../../shared/UI_service/media-control.service'
@Component({
  selector: 'app-clinic-location',
  templateUrl: './clinic-location.component.html',
  styleUrls: ['./clinic-location.component.less']
})
export class ClinicLocationComponent implements OnInit {
  @Input() clinics:Clinic[];
  thisDevice: boolean;
  
  constructor(private _viewMedia: MediaControlService) { }

  ngOnInit(): void {
  }
  ngAfterContentChecked(): void {
    this.thisDevice = this._viewMedia.mediaPort();
  }
}
