import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { MediaControlService } from '../../../shared/UI_service/media-control.service';
import { UserInputValidator } from '../../../services/userInputValidator'
import { Observable, Observer } from 'rxjs';
import { NzSafeAny } from 'ng-zorro-antd/core/types';
import { HttpConstants } from '../../../services/http-constants';
import { BaseHttpService } from '../../../services/base-http.service';
import { DoctorService } from '../../../services/doctor-service/doctor.service';
import { Patient } from '../../../interface/patient';
import { PatientService } from '../../../services/patient-service/patient.service';
import { AccountDetails } from '../../../interface/account-details';
import { DynamicTitleService } from 'src/app/shared/UI_service/dynamic-title.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.less']
})
export class LoginComponent implements OnInit {
  thisDevice: boolean = true;
  validateForm!: FormGroup;
  isLoading: boolean = false;
  submitForm(): void {

    for (const i in this.validateForm.controls) {
      this.validateForm.controls[i].markAsDirty();
      this.validateForm.controls[i].updateValueAndValidity();
    }
  }
  constructor(private _mediaControl: MediaControlService, private router: Router, private fb: FormBuilder, private baseHttpService: BaseHttpService, private doctorService: DoctorService, private patientService: PatientService, private activatedRoute: ActivatedRoute, private _dynamicTitle: DynamicTitleService) { }


  ngOnInit(): void {

    this.validateForm = this.fb.group({
      phoneNumber: ['', [Validators.required, Validators.maxLength(10), Validators.minLength(10), Validators.pattern('^[0-9]*$')], [this.userNameAsyncValidator]],
      phoneNumberPrefix: ['+91'],
      agree: [true, [Validators.required, Validators.requiredTrue]],
      otp: ['', [Validators.required, Validators.maxLength(4), Validators.minLength(4), Validators.pattern('^[0-9]*$')], [this.otpAsyncValidator]]
    });

    this.activatedRoute.params.subscribe((params) => {
      const token = params.token;
      if (token) {
        this.baseHttpService.makeAuthRequest<LoginInterface>("GET", HttpConstants.patient.getLoginDetails, null, token).subscribe((response) => {
          localStorage.setItem("token-" + this.doctorService.username, token);
          localStorage.setItem("patients" + this.doctorService.username, JSON.stringify(response.data.patients));
          this.router.navigate(["../dashboard"], {
            relativeTo: this.activatedRoute,
          });
          //  this.router.navigate(["/PatientDashboard"])

        });
      }
    });

    this._dynamicTitle.setPageTitle('login')
  }
  ngAfterContentChecked(): void {
    this.thisDevice = this._mediaControl.mediaPort()
  }


  sendingOtp: boolean = false;
  otpSent: boolean = false;
  timerId;
  timeLeft = 30;
  genOtpRes: any = "";

  countdown(self: LoginComponent) {
    console.log(self.timeLeft);

    if (self.timeLeft == 0) {
      clearTimeout(self.timerId);
    } else {
      self.timeLeft--;
    }
  }

  changeNumber() {
    this.genOtpRes = '';
    this.otpSent = false;
    this.validateForm.get('phoneNumber').setValue('');
    this.validateForm.get('otp').setValue('');
    this.validateForm.controls['phoneNumber'].enable();

  }

  userNameAsyncValidator = (control: FormControl) =>
    new Observable((observer: Observer<MyValidationErrors | null>) => {


      this.baseHttpService.makeRequest<boolean>({ method: "get", url: HttpConstants.otp.sendOtp + control.value }).subscribe((response) => {

        if (response.hasErrors()) {
          observer.next({
            duplicated: { en: `Somehing went wrong please try again` }
          });
        } else {
          var res: any = response.data;
          if (res.status) {
            this.otpSent = true;
            this.timerId = setInterval(() => {
              this.countdown(this);
            }, 1000);
            this.genOtpRes = res.otp;
            this.validateForm.controls['phoneNumber'].disable();

            observer.next(null);
          } else {
            observer.next({
              duplicated: { en: `Failed to send OTP. please try again` }
            });
          }
        }
        observer.complete();
      });

    });


  otpAsyncValidator = (control: FormControl) =>
    new Observable((observer: Observer<MyValidationErrors | null>) => {

      const otpToCompare = atob(this.genOtpRes);
      if (otpToCompare != control.value) {
        observer.next({
          duplicated: { en: `Please enter valid OTP` }
        });
      } else {
        if (this.validateForm.get('agree').value) {

          observer.next(null);
          this.login();
        } else {
          observer.next({
            duplicated: { en: `Please Accept T&C and click Login` }
          });

        }

      }

      observer.complete();



    });

  resendOTP() {

    this.isLoading = true;
    this.baseHttpService.makeRequest<boolean>({ method: "get", url: `${HttpConstants.otp.resendOtp}${this.validateForm.get('agree').value}/${this.genOtpRes}` }).subscribe((response) => {
      this.isLoading = true;

      if (response.hasErrors()) {
        alert(response.getErrorsText());
      } else {
        var res: any = response.data;
        if (res.status) {
          this.genOtpRes = res.otp;
          this.timerId = setInterval(() => {
            this.countdown(this);
          }, 1000);
        } else {
          alert("Failed to send otp please try again");
        }
      }
    });
  }




  login() {

    this.isLoading = true;
    console.log(this.validateForm.value.otp);

    const otpToCompare = atob(this.genOtpRes);
    if (otpToCompare != this.validateForm.get('otp').value) {
      return false;
    }
    if (!this.validateForm.get('agree').value) {
      return false;
    }

    const data = {
      doctorUsername: this.doctorService.username,
      mobileNumber: this.validateForm.get('phoneNumber').value,
      otpToVerify: this.genOtpRes,
    };
    this.baseHttpService.makeRequest<LoginInterface>({ method: "post", url: HttpConstants.otp.login, data }).subscribe((response) => {
      this.isLoading = false;

      if (response.hasErrors()) {
        alert(response.getErrorsText());
      } else {
        if (response.data) {
          // if (null) {
          localStorage.setItem("token-" + this.doctorService.username, response.data.token);
          localStorage.setItem("patients" + this.doctorService.username, JSON.stringify(response.data.patients));

          this.patientService.setSelectedPatient(response.data.patients[0]);
          this.router.navigate(["../dashboard"], {
            relativeTo: this.activatedRoute,
          });

        } else {

          this.router.navigate([`../Register/${this.validateForm.get('phoneNumber').value}`], {
            relativeTo: this.activatedRoute,
          });

        }
      }
    });
  }





}

interface SignUpInterface {
  token: string;
  patient: Patient;
}

interface LoginInterface {
  token: string;
  patients: Patient[];
}

export type MyErrorsOptions = { en: string } & Record<string, NzSafeAny>;
export type MyValidationErrors = Record<string, MyErrorsOptions>;

